@font-face {
  font-family: 'DM Sans';
  src: url(../fonts/dm-sans-v6-latin-ext_latin-regular.woff2) format('woff2'),
    url(../fonts/dm-sans-v6-latin-ext_latin-regular.woff) format('woff'),
    url(../fonts/dm-sans-v6-latin-ext_latin-regular.eot) format('eot'),
    url(../fonts/dm-sans-v6-latin-ext_latin-regular.ttf) format('ttf'),
    url(../fonts/dm-sans-v6-latin-ext_latin-regular.svg) format('svg');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

